	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"КРАСНАЯ ПЕСНЯ","type":"lyrics"},"html":"<h1>КРАСНАЯ ПЕСНЯ</h1>\n<section>\n<p>Вспомни<br>\nи спой мне</p>\n<p>Пусть рыбы станут нотами!<br>\nСпой мне о том, что ты жив.<br>\nСпой мне о том, что дредноуты<br>\nтонут в тебе, как моржи.<br>\nТонут и не всплывают,<br>\nоседают на дне.<br>\nКиты, титаники, сваи<br>\nгрозят твоей глубине!</p>\n<p>Выпусти рыб из горла — <br>\nначни свою красную песню!<br>\nПочувствуй, как ты оторван,<br>\nвымучен и неизвестен.<br>\nПочувствуй, как близко радость,<br>\nкак она пахнет шпалами,<br>\nснегом еловым и стадом,<br>\nкровью и караванами.<br>\nПусть птицы станут нотами!<br>\nСпой мне о том, кем ты был,<br>\nкогда были люди смотаны<br>\nв узел и брошены в ил.<br>\nСпой мне о том, что было<br>\nпосле войны за воду.<br>\nСпой нам, как церковь крестила<br>\nракеты, часы и заводы.</p>\n<p>Выпусти горлиц из горла — <br>\nначни свою красную песню!<br>\nПочувствуй, как ты оторван,<br>\nвымучен и неизвестен.<br>\nПочувствуй, как близко радость,<br>\nкак она пахнет ранами,<br>\nкровью и виноградом,<br>\nсолью и караваями.</p>\n<p>Высыпи землю из горла — <br>\nначни свою красную песню!<br>\nДрейфующий буревестник<br>\nзовёт на другую сторону.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/ru/lyrics/krasnayaPesnya/"}}

